.btn:hover {
  color: rgb(242, 234, 234);
  background-color: rgb(211, 84, 38);
}

.btn {
  margin-top: 4px;
  margin-bottom: 4px;
  width: 130px;
  border-radius: 10px;
  padding: 8px 16px;
  color: black;
  background-color: rgb(188, 217, 236);
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btnWrapper {
  margin-left: auto;
}
